import React, { Component } from "react";
import { CCard, CCardBody, CCardHeader, CCol, CRow } from "@coreui/react";

import api from "../../api";
import authHeader from "../../auth-header";
import { MDBDataTableV5 } from "mdbreact";
import moment from "moment";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class payments extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      loading: false,
    };
  }

  componentDidMount() {
    var info = [];
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_payments", {
      method: "POST",
      headers: authHeader(),
      // body: JSON.stringify(profile_data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        responseJsonFromServerData.map((item, key) => {
          //    var date = ;
          var arr = {
            shop: item.shopName,
            person: item.shopContactPerson,
            amount: item.amount / 100,
            method: item.method,
            card: item.cardId != null ? item.cardId : "---",
            bank: item.bank != null ? item.bank : "---",
            wallet: item.wallet != null ? item.wallet : "---",
            email: item.email,
            contact: item.contact,
            date: moment(item.paymemtAt).format("DD MMM YYYY hh:mm a"),
          };
          info.push(arr);
        });
        this.setState({
          data: info,
          loading: false,
        });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  render() {
    var datatable = {
      columns: [
        {
          label: "Shop Name",
          field: "shop",
          sort: "disabled",
          width: 150,
          attributes: {
            "aria-controls": "DataTable",
            "aria-label": "Icon",
          },
        },
        {
          label: "Contact Person",
          field: "person",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Amount",
          field: "amount",
          sort: "disabled",
          width: 150,
        },
        {
          label: "Method",
          field: "method",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Card Id",
          field: "card",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Bank",
          field: "bank",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Wallet",
          field: "wallet",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Email",
          field: "email",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Contact No.",
          field: "contact",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Payment Date",
          field: "date",
          sort: "disabled",
          width: 200,
        },
      ],
      rows: [...this.state.data],
    };
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Payment Details</h3>
                  </CCol>
                  <CCol sm="6" xs="6" md="4"></CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="12" sm="12" xs="12">
                    <MDBDataTableV5
                      hover
                      entriesOptions={[6, 10]}
                      entries={6}
                      pagesAmount={4}
                      data={datatable}
                      pagingTop
                      searchTop
                      searchBottom={false}
                      barReverse
                      responsive
                    />
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}
export default payments;
